<template>
  <v-app-bar height="73" app dark>
    <v-row class="justify-space-between align-center">
      <v-col :class="$vuetify.breakpoint.smAndUp ? 'vv' : 'nn'">
        <v-icon color="teal" size="50">mdi-ambulance</v-icon>
      </v-col>
     
      
        <v-row class="aling-center ">
      <v-spacer ></v-spacer>

          <v-col class="flex-grow-0">
            <v-btn text plain>
              <span class="mr-2 text-h4">طبيب</span>
            </v-btn>
          </v-col>
          <v-col class="flex-grow-0">
            <v-btn text plain>
              <div  class="v__div py-2 px-1 " style=" border-radius: 0 5px; font-size: 15px; ">
                <p class="mb-1 " style="font-weight: 600; color: rgb(10, 237, 215);">{{ visitCount }}</p>
                <p class="mb-0 " style="color: rgb(10, 237, 215);;">المشاهدات</p>
              </div>
            </v-btn>
          </v-col>
        </v-row>
      
    </v-row>
  </v-app-bar>
</template>

<script>
export default{

  data() {
    return {
      visitCount: 6000
    };
  },
  created() {
    // Load visit count from localStorage when the component is created
    this.loadVisitCount();
  },
  methods: {
    loadVisitCount() {
      // Load visit count from localStorage
      const storedCount = localStorage.getItem("Vcountt");
      if (storedCount) {
        this.visitCount = parseInt(storedCount);
      }
    },
    incrementVisitCount() {
      // Increment and save visit count to localStorage
      this.visitCount++;
      localStorage.setItem("Vcountt", this.visitCount.toString());
    }
  },
  mounted() {
    // Increment visit count when the component is mounted (website is visited)
    this.incrementVisitCount();
  }
}
</script>

<style>
.nn{
  display: none;
}
</style>